export default Vue.extend({
  name: "Home",
  components: {
    MobileTopBar,
    DesktopTopBar,
    HabitItem,
  },
  computed: {
    ...mapState({
      habits: "habits",
    }),
  },
  methods: {
    openPage(page: string) {
      this.$router.push(page);
    }
  },
});
